<template>
    <div class="allCouponList">
        <navigation-bar  @onLeftClick="onLeftClick" :pageName="couponType==1?'平台红包':'店铺优惠券'" ref="navigation"></navigation-bar>
        <div class="scrollY" ref="scrollY">
            <van-pull-refresh v-model="isRefreshLoading" @refresh="onRefresh">
                <van-list
                        v-model="loading"
                        :finished="finished"
                        :style="{minHeight:scrollYHeight+'px'}"
                        @load="onLoad"
                        v-if="lists.length > 0"
                        :offset="30"
                        :immediate-check="immediateBol"
                >
                    <div class="topSpace"></div>
                    <coupon-item :lists="lists"/>
                </van-list>
                <div v-if="isNoNetwork">
                    <NetworkError></NetworkError>
                </div>
            </van-pull-refresh>
        </div>
    </div>
</template>


<script>
    import {getUserCouponList,getUserPlatformList } from "../../services/orderListService";
    import NavigationBar from '@/common/currency/NavigationBar.vue';
    import couponItem from '@/page/couponList/couponItem'
    import NetworkError from '@/common/networkError/NetworkError'
    import { Toast, Dialog,ActionSheet  } from "vant";
    export default {
        name:"allCouponList",
        components:{
            NavigationBar,
            couponItem,
            NetworkError
        },
        data(){
            return{
                immediateBol:false,
                finished:false,
                loading:false,
                isRefreshLoading:false,
                couponType:null,
                statusType:null,
                page:1,
                lists:[],
                isNoNetwork:false, //是否没有网络
                scrollYHeight:0,
            }
        },
        mounted(){

        },
        activated(){
            if(this.$route.params.couponType){
                this.couponType = this.$route.params.couponType;
                this.statusType = this.$route.params.statusType;
                this.$nextTick(()=>{
                    console.log()
                    this.scrollYHeight = this.$refs.scrollY.clientHeight
                })
                this.init();
            }else{

            }
        },
        deactivated(){

        },
        watch:{

        },
        computed: {

        },
        methods:{
            //初始化数据
            init(){
                this.page = 1;
                this.lists = [];
                this.loading = false;
                this.finished = false;
                this.isNoNetwork = false;
                if(this.couponType == 1){
                    this.queryByUserV33(1); //平台优惠券
                }else{
                    this.queryByUser(1);  //店铺优惠券
                }
            },
            queryByUser(pageNumber,isFirst = true){
                const paramData = {
                    page:pageNumber,
                    rows:10,
                    ownerCode:this.$userInfo.OWNER_CODE,
                    status:this.statusType,
                }
                getUserCouponList(paramData).then(res => {
                    if (res.resultCode == 0) {
                        if(isFirst){
                            this.lists = res.data.couponScopeList;
                        }else{
                            this.lists = this.lists.concat(res.data.couponScopeList);
                        }
                        this.isLoading = false;
                        this.loading = false;
                        this.isRefreshLoading = false;
                        this.finished = this.lists.length>=res.total?true:false;
                    }
                }).catch(error => {
                    this.couponList = [];
                    this.isRefreshLoading = false;
                    this.isLoading = true;
                    this.isNoNetwork = true;
                });
            },
            queryByUserV33(pageNumber,isFirst = true){
                const paramData = {
                    page:pageNumber,
                    rows:10,
                    ownerCode:this.$userInfo.OWNER_CODE,
                    status:this.statusType,
                }
                getUserPlatformList(paramData).then(res => {
                    if (res.resultCode == 0) {
                        if(isFirst){
                            this.lists = res.data.platformList;
                        }else{
                            this.lists = this.lists.concat(res.data.platformList);
                        }
                        this.isLoading = false;
                        this.loading = false;
                        this.isRefreshLoading = false;
                        this.finished = this.lists.length>=res.total?true:false;
                    }
                }).catch(error => {
                    this.couponList = [];
                    this.isRefreshLoading = false;
                    this.isLoading = true;
                    this.isNoNetwork = true;
                });
            },
            //下拉刷新
            onRefresh(){
                this.init();
            },

            onLoad(){
                this.page++;
                console.log(this.page);
                if(this.couponType == 1){
                    this.queryByUserV33(this.page,false); //平台优惠券
                }else{
                    this.queryByUser(this.page,false);  //店铺优惠券
                }
            },
            //返回键
            onLeftClick() {
                this.$router.go(-1);//返回上一层
            },
        },
        beforeRouteLeave(to, form, next) {

            next();
        }
    }
</script>

<style scoped lang="scss">
    .network{
        background: #f9f9f9;
    }
    .allCouponList{
        width: 100%;
        height: 100%;
        background-color: #f9f9f9;
        display: flex;
        flex-direction: column;
    }
    .scrollY{
        height: 100%;
        overflow-y: auto;
        .van-pull-refresh{
            min-height: 100%;
        }
    }
    .topSpace{
        width: 100%;
        height: 15px;
    }
    .bottomSpace {
        width: 100%;
        height: 68px;
    }
</style>
